
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import { thinkingStatsFake } from "@/fakebackend/thinkingStatsFake";
import svgComponentVue from "@/utils/svgComponent.vue";
import { IThinkingBallonFake } from "@/fakebackend/thinkingBallonFake";
export default defineComponent({
  name: "cDevPetsThinking",
  components: { svgComponentVue },
  mixins: [NuiComponentMixin],
  data() {
    return {
      left: 0,
      top: 0,
      scale: 1,
      leftStats: 0,
      topStats: 0,
      scaleStats: 1,
      thinkingStatsList: thinkingStatsFake,
      thinkingBallonList: IThinkingBallonFake,
      thinkingBallon: false,
      thinkingStats: false,

      moodTimeoutId: 0,
      statIndex: 0,
    };
  },
  props: {
    name: {
      type: String,
    },
  },
  methods: {
    getMarginLeft() {
      let element = this.$refs["ct-pets-thinking"] as HTMLElement;

      if (element) {
        return -(element.offsetWidth / 2);
      }
    },
    getMarginLeftStats() {
      let element = this.$refs["ct-pets-thinking-stats"] as HTMLElement;

      if (element) {
        return -(element.offsetWidth / 2);
      }
    },
    handleNUI({ action, data }: { action: string; data: unknown }) {
      if (action === "setPos") {
        let props = data as { left: number; top: number };

        this.left = props.left;
        this.top = props.top;
      } else if (action === "setPosStats") {
        let props = data as { left: number; top: number };

        this.leftStats = props.left;
        this.topStats = props.top;
      } else if (action === "setScale") {
        let props = data as { scale: number };

        this.scale = props.scale;
      } else if (action === "setScaleStats") {
        let props = data as { scale: number };

        this.scaleStats = props.scale;
      } else if (action === "showMood") {
        let props = data as { mood: string; duration: number };

        this.thinkingBallonList.icon = props.mood;
        this.thinkingBallon = true;

        let timeoutId = this.moodTimeoutId = setTimeout(() => {
          if (this.moodTimeoutId !== timeoutId) {
            return;
          }

          this.thinkingBallon = false;
        }, props.duration);
      } else if (action === "clearMood") {
        this.thinkingBallon = false;
        this.moodTimeoutId = 0;
      } else if (action === "showStat") {
        let props = data as { stat: string; decreased: boolean; duration: number };
        let index = this.statIndex++;

        this.thinkingStatsList.push({
          icon: props.stat,
          add: !props.decreased,
          index,
        });

        this.thinkingStats = true;

        this.$nextTick(() => {
          const stat = this.thinkingStatsList.find((s) => s.index === index);
          if (stat) {
            stat.fade = true;
          }
        });

        setTimeout(() => {
          this.thinkingStatsList = this.thinkingStatsList.filter((s) => s.index !== index);

          if (this.thinkingStatsList.length === 0) {
            this.thinkingStats = false;
          }
        }, props.duration);
      } else if (action === "clearStats") {
        this.thinkingStats = false;
      }
    },
  },
  mounted() {
    //window.addEventListener("keydown", this.onKeyDown);
  },
  beforeUnmount() {
    //window.removeEventListener("keydown", this.onKeyDown);
  },
});
